body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card{
	background: -webkit-linear-gradient(bottom, rgb(19, 151, 70), lightblue, rgb(19, 151, 70));
	background: linear-gradient(to top, rgb(19, 151, 70), lightblue, rgb(19, 151, 70));
	color: transparent;
	margin-top: 1rem;
	margin-left: 4rem;
	margin-right: 4rem;
	display: inline-block;
}

img, #image-container, .card{
	width: 150px;
	height: 311px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.wrapper{
  background-color: rgb(19, 151, 70);
}

h1{
  color: red;
  text-shadow: -2px 0 yellow, 0 2px yellow, 
  2px 0 yellow, 0 -2px yellow;
}

html{
  background-color: rgb(53, 99, 173);
  text-align: center;
}

.title{
  background-color: rgb(114, 114, 114);
  text-align: center;
  border-left: none;
  border-right: none;
}
